//globalVar=require('./core/global-variable.js');//
require("./core/main-nav.js");
//
var navO=$('#mian-nav');
var footerO=$('.footer-con');
var newsO=$('.game-news');
var throttleTimer=null;
pageReset();
//
$(window).on('resize',function(){
    clearTimeout(throttleTimer);
    throttleTimer=setTimeout(function(){
        pageReset();
    },300);
});
//
setNewsNavActive();//
function setNewsNavActive(){
    var newYear=2016;
    var hrefStr=location.href;
    for(var i=0;i<10;i++){
        if(hrefStr.indexOf(newYear)!=-1){
            break;
        }
        newYear++;
    }
    if(i>=10){
        return;
    }
    $('.game-news__nav-con').find("[href*=-"+newYear+"]").addClass('active');
}
//
function pageReset(){
    var winH=$(window).height();
    var navOH=navO.height();
    var footerOH=footerO.outerHeight();
    var conH=navOH+footerOH+newsO.height();
    if(winH>conH){
        newsO.css({'height':winH-navOH-footerOH+'px'});
    }
}


